.sample-options {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 8px;
}

.sample-grid {
  height: 100%;
  display: grid;
  gap: 16px;
  grid-template-columns: 1fr 200px;
  grid-template-rows: 1fr min-content;
}

.elements-grid {
  display: grid;
  gap: 8px;
  grid: "control info" min-content "table table" 1fr/auto 1fr;
  overflow-y: hidden;
}
.elements-grid-a {
  grid-area: control;
}
.elements-grid-b {
  grid-area: table;
  height: 100%;
  overflow-y: scroll;
}
.elements-grid-c {
  grid-area: info;
}

.controls-grid {
  display: grid;
  gap: 8px;
  grid: min-content/1fr;
}

.color-picker {
  display: flex;
  gap: 8px;
  align-content: center;
}

.control-button {
  width: 100%;
}

.instructions {
  grid-row: 2;
  grid-column: 1/span 2;
}